html {
  box-sizing: border-box;
  font-size: 4vw;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  user-select: none;
  outline: none;
}


input, textarea {
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Mina", sans-serif;
}

body.dont-scroll {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

::-webkit-scrollbar {
  display: none;
}

.crazy-rocket {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  background-image: linear-gradient(0, #0e0e1c 0, #060608 100%);
  z-index: 1;
  position: relative;
  overflow: hidden;

  &::after,
  &::before {
    position: fixed;
    content: "";
    display: block;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: 100% 100%;
    background-position: center ;
  }
}

.crazy-rocket::after {
  background-image: url("../img/stars-z1.png");
  z-index: -3;
}

.crazy-rocket::before {
  background-image: url("../img/stars-z2.png");
  z-index: -2;
}
