.crazy-rocket.ready::after {
  animation-name: sky;
  animation-duration: 200s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.crazy-rocket.ready::before {
  animation-name: sky;
  animation-duration: 140s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.blinker span {
  animation-name: blinker;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.console__bet-button-layer {
  animation-name: rotate;
  animation-duration: 17s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.console-top__arrow-field {
  animation-name: speed;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.rocket {
  animation-name: rocket-animation;
  animation-duration: 1s;
  animation-timing-function: steps(11);
  animation-iteration-count: infinite;
}

.crazy-rocket.start {
  .rocket-field {
    animation-name: fly;
    animation-duration: 0.4s;
    animation-delay: 4s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }

  .time-field {
    animation-name: time-hidden;
    animation-duration: 4s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }

  .time {
    animation-name: time;
    animation-duration: 4s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }

  .background-z1,
  .background-z2,
  .rocket-garage {
    animation-name: go-down;
    animation-duration: 0.4s;
    animation-delay: 4s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }

  .rocket__coefficient-field,
  .rocket__coefficient-win {
    animation-name: coefficient;
    animation-duration: 0.3s;
    animation-delay: 4s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }
}

.crazy-rocket.background-fast {
  &::after {
    animation-name: sky-fast;
    animation-duration: 70s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }

  &::before {
    animation-name: sky-fast;
    animation-duration: 30s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
  }
}


.crazy-rocket.end {
  .rocket-inner {
    animation-name: fly-end;
    animation-duration: 0.5s;
    animation-delay: 0.1s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }

  .rocket__coefficient-field {
    animation-name: coefficient-up;
    animation-duration: 0.2s;
    animation-delay: 0.2s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }

  .rocket__coefficient-background {
    animation-name: coefficient-background;
    animation-duration: 0.2s;
    animation-delay: 0.2s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }

  .rocket__coefficient-win {
    animation-name: coefficient-win;
    animation-duration: 0.2s;
    animation-delay: 0.2s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
  }

  .console-top__next-round {
    transform: scale(1);
    opacity: 1;
  }

  .console-top__circle-field {
    display: none;
  }

  .console-top {
    background-image: none;
  }
}

@keyframes sky {
  100% {
    background-position: center 5000px;
  }
}


@keyframes blinker {
  50% {
    opacity: 0;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes speed {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(-36deg);
  }

  75% {
    transform: rotate(36deg);
  }

  100% {

    transform: rotate(0deg);
  }
}

@keyframes rocket-animation {
  0% {
    background-position-y: 0;
  }

  100% {
    background-position-y: 110%;
  }
}

@keyframes fly {
  0% {
    transform: translate(-50%, 0);
  }

  100% {
    transform: translate(-50%, -55%);
  }
}

@keyframes time-hidden {
  0% {
    opacity: 1;
  }

  99% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes time {
  0% {
    width: 100%;
  }

  100% {
    width: 0;
  }
}

@keyframes go-down {
  0% {
    transform: translate(-50%, 0);
  }

  100% {
    transform: translate(-50%, 100rem);
  }
}

@keyframes sky-fast {
  100% {
    background-position: center 5000px;
  }
}

@keyframes fly-end {
  100% {
    transform: translateY(-200rem);
  }
}

@keyframes coefficient {
  0% {
    transform: translate(-50%, 0) scale(0.1);
    opacity: 0;
  }

  10% {
    opacity: 1;
  }

  100% {
    transform: translate(-50%, 0) scale(1);
    opacity: 1;
  }
}

@keyframes coefficient-background {
  0% {
    opacity: 0;
    transform: scale(0.1);
    color: rgba(var(--rocket-green1), 0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
    color: rgba(var(--rocket-green1), 0.8);
  }
}

@keyframes coefficient-win {
  0% {
    transform: translateX(4rem) scale(0.1);
    color: rgba(var(--rocket-green1), 0);
  }

  100% {
    transform: translateX(4rem) scale(1);
    color: rgba(var(--rocket-green1), 0.8);
  }
}

@keyframes coefficient-up {
  0% {
    transform: translate(-50%, 0) scale(0.1);
    opacity: 1;
  }

  100% {
    transform: translate(-50%, -0.8rem) scale(1);
    opacity: 1;
  }
}

.crazy-rocket.end {
  &::after {
    animation: sky-end 3s ease-out forwards;
  }

  &::before {
    animation: sky-end 3s ease-out forwards;
  }
}

@keyframes sky-end {
  100% {
    background-position: center 600px;
  }
}

