@keyframes sky {
  100% {
    background-position: center 5000px; } }

@keyframes blinker {
  50% {
    opacity: 0; } }

@keyframes rotate {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes speed {
  0% {
    transform: rotate(0deg); }
  25% {
    transform: rotate(-36deg); }
  75% {
    transform: rotate(36deg); }
  100% {
    transform: rotate(0deg); } }

@keyframes rocket-animation {
  0% {
    background-position-y: 0; }
  100% {
    background-position-y: 110%; } }

@keyframes fly {
  0% {
    transform: translate(-50%, 0); }
  100% {
    transform: translate(-50%, -55%); } }

@keyframes time-hidden {
  0% {
    opacity: 1; }
  99% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes time {
  0% {
    width: 100%; }
  100% {
    width: 0; } }

@keyframes go-down {
  0% {
    transform: translate(-50%, 0); }
  100% {
    transform: translate(-50%, 100rem); } }

@keyframes sky-fast {
  100% {
    background-position: center 5000px; } }

@keyframes fly-end {
  100% {
    transform: translateY(-200rem); } }

@keyframes coefficient {
  0% {
    transform: translate(-50%, 0) scale(0.1);
    opacity: 0; }
  10% {
    opacity: 1; }
  100% {
    transform: translate(-50%, 0) scale(1);
    opacity: 1; } }

@keyframes coefficient-background {
  0% {
    opacity: 0;
    transform: scale(0.1);
    color: rgba(var(--rocket-green1), 0); }
  100% {
    opacity: 1;
    transform: scale(1);
    color: rgba(var(--rocket-green1), 0.8); } }

@keyframes coefficient-win {
  0% {
    transform: translateX(4rem) scale(0.1);
    color: rgba(var(--rocket-green1), 0); }
  100% {
    transform: translateX(4rem) scale(1);
    color: rgba(var(--rocket-green1), 0.8); } }

@keyframes coefficient-up {
  0% {
    transform: translate(-50%, 0) scale(0.1);
    opacity: 1; }
  100% {
    transform: translate(-50%, -0.8rem) scale(1);
    opacity: 1; } }

.crazy-rocket.end::after {
