.crazy-rocket.crazy-rocket__landscape {
  background-image: url("../../img/landscape-background.png");
  background-position: center;
  background-size: cover;
  align-items: center;
  display: flex;
  justify-content: center; }
  .crazy-rocket.crazy-rocket__landscape::after {
    display: none; }
  .crazy-rocket.crazy-rocket__landscape::before {
    display: none; }

.landscape__icon {
  width: 50vh;
  height: 50vh;
  animation-name: landscape-animation;
  animation-iteration-count: infinite;
  animation-duration: 3s;
  animation-timing-function: ease-in-out; }

.landscape__icons-svg {
  width: 100%;
  height: 100%; }

@keyframes landscape-animation {
  0% {
    transform: rotate(0); }
  50% {
    transform: rotate(45deg); }
  100% {
    transform: rotate(0); } }

.landscape {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url("../../img/landscape-background.png");
  z-index: 10;
  align-items: center;
  justify-content: center;
  background-size: cover; }

@media (orientation: landscape) {
  .landscape {
    display: flex; } }
